@import 'assets/scss/variables';

.movie-genre-layout {
	position: relative;
	.movie-genere-header {
		position: sticky;
		top: 0;
		right: 0;
		left: 0;
		width: 100%;
		padding: 0 1.4rem 0 1.4rem;
		box-sizing: border-box;
		.movie-genere-nav {
			z-index: 2;
			display: flex;
			justify-content: space-between;
			align-items: center;
			height: $header-height;
			.genere-nav-header {
				display: flex;
				align-items: center;
				gap: 1rem;
				flex: 1;
				input {
					width: 80%;
					border: none;
					outline: none;
					height: 2.5rem;
					color: $primary-text-color;
					background-color: $nav-bg-color;
					padding-left: 1rem;
				}
				h1 {
					font-size: $font-size-h1;
					font-weight: 400;
				}
			}
			.genere-nav-icon {
				width: $icon-size;
				height: $icon-size;
				cursor: pointer;
			}
			.genere-nav-img {
				position: absolute;
				z-index: -1;
				width: 100%;
				top: 0;
				left: 0;
				right: 0;
				height: $header-image-height;
			}
		}
	}
	main {
		padding: 2rem 1.4rem 0 1.4rem;
	}
}
