.movie-container {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	gap: 1.6rem;
}

@media (min-width: 1200px) {
	.movie-container {
		grid-template-columns: repeat(6, 1fr);
		gap: 2rem;
	}
}

@media (min-width: 800px) {
	.movie-container {
		grid-template-columns: repeat(4, 1fr);
		gap: 2rem;
	}
}
